* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
}
.logo {
  font-size: 20px;
}

.spinner {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 10px solid rgba(255, 0, 0, 0.397);
  border-top-color: red;
  animation: loader 1s infinite linear;
  margin: 50px auto;
}

@keyframes loader {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
